import { NzModalService } from 'ng-zorro-antd/modal';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { VhAlgorithm, VhQuery, VhQueryCafe } from 'ionic-vhframeworks';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';

@Component({
  selector: 'app-label-products',
  templateUrl: './label-products.component.html',
  styleUrls: ['./label-products.component.scss']
})
export class LabelProductsComponent implements OnInit {
  list_barcode: any = [];
  showModalAdd = false;
  showModalEdit = false;
  id_barcode: any = '';
  formAdd: FormGroup;
  barcode_name = ''
  constructor(
    private router: Router,
    public languageService: LanguageService,
    private nzModalService: NzModalService,
    private vhQuery: VhQuery,
    private vhComponent: VhComponent,
    private vhQueryCafe: VhQueryCafe,
    private vhAlgorithm: VhAlgorithm
  ) { }

  ngOnInit(): void {
    this.getData();
    this.initForm();
  }

  getData() {
    this.vhComponent.showLoading('').then(() => {
      this.vhQuery.getDocsByFields("barcode_designs", { id_branch: { $eq: this.vhQueryCafe.getDefaultBranch()._id }, type : {$eq: 6}  })
        .then((response: any) => {
          this.list_barcode = response.map(item => { item.property = this.vhAlgorithm.sortNumberbyASC(item.property, 'index'); return { ...item } })
          this.vhComponent.hideLoading(0);  
        })
    })
  }

  initForm() {
    this.formAdd = new FormGroup({
      name: new FormControl('', Validators.required),
      // col: new FormControl(2, Validators.required),
      width: new FormControl(35, Validators.required),
      height: new FormControl(22, Validators.required),
      type: new FormControl(6)
    })
  }
  addBarcode() {
    let w = this.formAdd.value.width
    let h = this.formAdd.value.height
    if (h > w) { this.formAdd.controls['width'].setValue(h); this.formAdd.controls['height'].setValue(w); }
    this.vhComponent.showLoading('').then(() => {
      this.vhQuery.addDoc("barcode_designs", {
        ...this.formAdd.value, id_branch: this.vhQueryCafe.getDefaultBranch()._id, property: [{
          type: 'name',
          font_family:'sans-serif',
          font_size: 12,
          text_align: 'center',
          status: true,
          value: "Sản phẩm 1",
          font_weight: 'bold', // bold | 500
          font_style: 'normal', // normal | italic
          text_decoration: 'none', // underline | none
          white_space: 'nowrap', // nowrap| normal
          index: 0,
          z_index: 2,
          y: 5,
          x: 0,
          height: (this.formAdd.value.height) * 0.2,
          width: (this.formAdd.value.width-1),
        },
        {
          type: 'barcode',
          font_size: 12,
          text_align: 'center',
          status: true,
          value: "123456789",
          font_weight: 'bold', // bold | 500
          font_style: 'normal', // normal | italic
          text_decoration: 'none', // underline | none
          white_space: 'nowrap', // nowrap| normal
          y: (this.formAdd.value.height) * 0.2 *3.78,
          x: 0,
          height: parseFloat(((this.formAdd.value.height) * 0.4).toString()).toFixed(1),
          index: 1,
          z_index: 1,
          width: this.formAdd.value.width - 2,
          justify_content: 'center',
        },
        {
          type: 'barcode-text',
          font_size: 10,
          font_family:'sans-serif',
          font_weight: 'bold', // bold | 500
          font_style: 'normal', // normal | italic
          text_decoration: 'none', // underline | none
          white_space: 'nowrap', // nowrap| normal
          text_align: 'center',
          status: true,
          value: "123456789",
          y: (this.formAdd.value.height) * 0.6 *3.78  ,
          x: 0,
          index: 2,
          z_index: 2,
          height: (this.formAdd.value.height) * 0.15,
          width: (this.formAdd.value.width)-1,
        },
        {
          type: 'price',
          font_size: 12,
          font_family:'sans-serif',
          text_align: 'center',
          status: true,
          value: 100000,
          font_weight: 'bold', // bold | 500
          font_style: 'normal', // normal | italic
          text_decoration: 'none', // underline | none
          white_space: 'nowrap', // nowrap| normal
          y: (this.formAdd.value.height) * 0.75 *3.78 ,
          x: 0,
          index: 3,
          z_index: 2,
          height: (this.formAdd.value.height) * 0.15,
          width: (this.formAdd.value.width)-1,
        },
        {
          type: 'label',
          font_size: 10,
          font_family:'sans-serif',
          text_align: 'left',
          status: true,
          value: 'Nhãn',
          font_weight: 'bold', // bold | 500
          font_style: 'italic', // normal | italic
          text_decoration: 'none', // underline | none
          white_space: 'nowrap', // nowrap| normal
          y: (this.formAdd.value.height) * 0.85 *3.78 ,
          x: 0,
          index: 4,
          z_index: 2,
          height: (this.formAdd.value.height) * 0.12,
          width: (this.formAdd.value.width-1),
        }
      ]
      })
        .then((response) => {
          console.log(response);
          
          this.vhComponent.hideLoading(0);
          this.list_barcode.push(response);
          this.showModalAdd = false;
        }, error => {
          this.vhComponent.hideLoading(0);
          this.vhComponent.alertMessageDesktop("error", this.languageService.translate("An error while adding price list"))
        })
    })
  }

  editBarcode() {
    if (this.barcode_name != '')
      this.vhComponent.showLoading('').then(() => {
        this.vhQuery.updateDoc("barcode_designs", this.id_barcode, { name: this.barcode_name }).then((bool) => {
          this.vhComponent.hideLoading(0);
          this.showModalEdit = false;
          this.list_barcode[this.list_barcode.findIndex(item => item._id == this.id_barcode)].name = this.barcode_name;
        }, error => {
          this.vhComponent.hideLoading(0);
          console.log('error', error);
        })
      })

  }

  gotoEdit(item) {
    this.router.navigate(['/cafe/dashboard/management/design/design-label/label-products/detail'], { state: item });
    // switch(item.col){
    //   case 2:{
    //     this.router.navigate(['/cafe/dashboard/management/design/design-label/label-products/detail'], { state: item });
    //     break;
    //   }
    //   case 3:{
    //     this.router.navigate(['/cafe/dashboard/management/design/design-label/label-products/detail-three-col'], { state: item });
    //     break;
    //   }
    //   default :{
    //     this.vhComponent.alertMessageDesktop( "error",this.languageService.translate("Không hỗ trợ kiểu tem này"))
    //   }
    // }
  }
  goBack() {
    this.router.navigate(['/cafe/dashboard/management/design/design-label'])
  }

  deleteBarcode(item, index) {
    this.nzModalService.confirm({
      nzTitle: this.languageService.translate("Are you sure to delete this label template?"),
      nzCancelText: this.languageService.translate("Cancel"),
      nzOnOk: () => {
        this.vhComponent.showLoading('').then(() => {
          if (this.vhQueryCafe.getlocalProducts().filter(i => i.id_design_barcode == item._id).length) {
            this.nzModalService.error({
              nzTitle: this.languageService.translate("Mẫu tem này đang liên kết với sản phẩm, không thể xóa!"),
              nzCancelText: this.languageService.translate("Cancel"),
            })
            this.vhComponent.hideLoading(0);
          }
          else
            this.vhQuery.deleteDoc("barcode_designs", item._id)
              .then((bool) => {
                this.list_barcode.splice(index, 1)
                this.vhComponent.hideLoading(0);
              })
        })
      },
    })
  }

}
